.charasters {
  position: relative;
}

.char_list {
  display: flex;
  margin-top: 25px;
}
.paginations {
  width: 65%;
}
.paginations button {
  width: 28%;
  margin: 20px 32%;
}
button {
  padding: 11px 18px;
  clip-path: polygon(0% 25%, 10% 0%, 100% 0%, 100% 75%, 90% 100%, 0% 100%);
}
@media (max-width: 1100px) {
  .paginations {
    width: 100%;
  }
}
